import React, { useContext, useMemo } from 'react'
import {
  Box, IconButton, SxProps, Theme, Typography
} from "@mui/material"
import ReactMarkdown from 'react-markdown'
import GameContext from '../../GameContext'
import AppContext from '../../AppContext'
import { Cancel } from '@mui/icons-material'


const IntroBox = () => {
  const { nodeConfig } = useContext(AppContext)
  const { gameNodes, hoverGameNode, selectedGameNode, handleClickGameNode } = useContext(GameContext)

  const targetNode = hoverGameNode || selectedGameNode

  const imgStyle = useMemo(() => {
    if ( targetNode === null ) return {}
    else if ( gameNodes[targetNode].metadata.count === 0 ) {
      return { 
        filter: "grayscale(1)",
      }
    }
    return {}
  }, [gameNodes, targetNode])

  if ( targetNode === null ) {
    return (<></>)
  }

  const { label, metadata: { description } } = gameNodes[targetNode]

  return (
    <Box sx={rootSx}>
      <Box 
        sx={{
          ...imageSx,
          ...imgStyle,
          backgroundImage: `url(${nodeConfig[targetNode].backgroundImage})`
        }}
      />
      <Box>
        <Box sx={headerSx}>
          <Typography variant="h4">{label}</Typography>
          {
            ( hoverGameNode === null || hoverGameNode === selectedGameNode ) && (
              <IconButton onClick={() => handleClickGameNode(null)}>
                <Cancel />
              </IconButton>
            )
          }
        </Box>
        <ReactMarkdown>
          {description}
        </ReactMarkdown>
      </Box>
    </Box>
  )
}

export default IntroBox

const rootSx: SxProps<Theme> = {
  backdropFilter: 'blur(0.5rem)',
  background: "rgba(255, 255, 255, 0.4)",
  borderRadius: 1,
  position: 'fixed',
  maxWidth: 400,
  mb: 2,
  mx: 1,
  p: 1,
  bottom: 0,
  display: 'flex',
}

const imageSx: SxProps<Theme> = {
  width: '40%',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  mr: 1, 
}

const headerSx: SxProps<Theme> = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  py: 1,
}